@import url(https://fonts.googleapis.com/css2?family=Montserrat&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Staatliches&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Montserrat&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Alfa+Slab+One&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Montserrat&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Alfa+Slab+One&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Playfair+Display+SC&display=swap);

body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

html{
  font-size: 62.5;
  box-sizing: border-box;
}

body{
  background-color: #181818;
}
*{
  scroll-behavior: smooth;
}



.logo{
  display: inline;
  justify-content: center;
  margin-top: 2%;
 z-index: 100;
 position: relative;
}

.nav{
  color:#fff;
  padding: 0 0 4px;
  border-bottom: 4px solid #0D94FB;

}

.heading{
  display:flex;
  position: fixed ;
  width: 100%;
  cursor:pointer;
  justify-content:center;
  padding-bottom: 15px;
  box-shadow: 0px 1px 28px  #0D94FB;
  z-index: 100;
  background-color: #181818;
}

.trending{
  justify-content: center;
  align-items: center;
}

a{
  color:whitesmoke;
}
.filterdata{
  background-color: transparent;
  border-color: #0D94FB;
  margin: 1px;
}

@media (max-width: 1000px) {
  .heading {
    padding-top: 15px;
    font-size: 6.4vw;
  }
}

.App {
  background-color: #191919;
  color: blanchedalmond ;
  font-family: Staatliches;
  
}



@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}
.App-logo {
  height: 40vmin;
  pointer-events: none;
}


header a{
  color: white;
}
figcaption{
  position: absolute;
  bottom: 2rem;
  margin: 2rem;
  font-size: 5rem;
  color: white;
}
.image{
  width: 100%;
  height:100%;
}
.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

.App{
  position: relative;
  min-height: 100vh;
 padding-top: 130px;
  padding-bottom:70px;
  background: rgb(0,0,0,0.8) url(/MovieTips/static/media/background.58eb0a0f.jpg);
  background-blend-mode: darken;
  left:0;
  
}

.pageTitle {
  text-transform: uppercase;
  display: flex;
  justify-content: center;
  font-family: "Staatliches";
  font-size: 2vw;
  padding: 4px;
  border-radius: 50px;
  color: white;
}

.movies{
  position: absolute;
}

@media (max-width:700px){
  .app{
    padding-top:70px;
  }
}
@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}

.media{
    width:247px;
    height: 380px;
    display: flex;
    border-radius: 14px;
    top:5%;
    left: 0;
    margin-right: 50px ;
    margin-top: 50px;
    margin-bottom: 5px;
    flex-direction: column;
    height: 380px;
    position: relative;    
     flex-wrap: wrap;
    justify-content: space-around;
    transition: 0.5s;
    flex:1 1;
    background-color: linear-gradient(to top,white,white);
   
    
}



