.media img{
     width:250px;
     display: flex;
     border-radius: 20px;
     justify-content: space-around;
     top:0;
     height: 100%;
     position: relative;
 }

.content{
 
    position: relative;
    color: white;
    opacity: 0;
    transform: translateY(0px);
}


.media:hover{
    box-shadow:transparent ;
}

.media:hover .content{
    display: flex;
    opacity: 1; 
    transform: translate(0px,100px);
    flex-wrap: wrap;
    flex-direction: column;
    justify-content: center;
    border-radius: 2px;
    position: absolute;
    transition: 0.5s ; 
    color:#0D94FB;
    background-color: linear-gradient(to top,rgb(0,0,0,0.6),rgb(40,40,43));
    font-family:Montserrat ;
    font-size: 1.5rem;


}

.media:hover img{
    opacity: 0.5;
    
    box-shadow: 0 25px 40px #0D94FB;
}

.media .content{
    
    display:flex;
    font-family:Montserrat ;
    font-size: 1.5rem;
    color:#0D94FB;
    transform: translate(0px,0px);
    transition: 0.5s;
    position: absolute;
    background-color: linear-gradient(to top,rgb(0,0,0,0.6),rgb(40,40,43));
}

.content span,p,strong{
    color: whitesmoke;
}









@media (max-width: 550px) {
    .media {
      width: 150px;
    }
  }





